<template>
  <div class="ParticipateProjectTotalBackground">
    <!--     <input
                  type="text"
                  class="ParticipateProjectUserNumberInput"
                  v-model="ParticipateProjectUserNumber"
                  placeholder="请输入学生学号"
                />
                <button  class="ParticipateProjectSeekButton" @click="ParticipateProjectSeekButtonClick">查询</button>
                <button  class="ParticipateProjectEditButton" @click="ParticipateProjectEditButtonClick">编辑</button>
             -->
    <div class="ParticipateProjectTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 2%">姓名</th>
            <th style="width: 6%">项目名称</th>
            <th style="width: 6%">项目来源</th>
            <th style="width: 6%">项目形式</th>
            <th style="width: 2%">排名</th>
            <th style="width: 2%">开发技术</th>
            <th style="width: 2%">指导老师</th>
            <th style="width: 2%">开发时间</th>
            <th style="width: 2%">项目历时</th>
            <th style="width: 1%">有效代码数</th>
            <th style="width: 2%">主要创新点</th>
            <th style="width: 2%">主要功能</th>
            <th style="width: 2%">对应文档</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="records in StudentParticipateProjectTable"
            :key="records.participateProjectDetails[0].studentNumber"
          >
            <td>{{ records.participateProjectDetails[0].studentName }}</td>
            <td>{{ records.participateProjectDetails[0].projectName }}</td>
            <td>{{ records.participateProjectDetails[0].projectSource }}</td>
            <td>
              {{ records.participateProjectDetails[0].type }}
            </td>
            <td>{{ records.participateProjectDetails[0].teamRank }}</td>
            <td>
              {{ records.participateProjectDetails[0].developTechnology }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].instructor }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].developTime }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].duration }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].numberOfValidCodes }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].mainInnovations }}
            </td>
            <td>
              {{ records.participateProjectDetails[0].mainFunction }}
            </td>
            <td>
              <a
                href="{{  records.participateProjectDetails[0].signUpFile }}"
                target="_blank"
                >查看文档</a
              >
            </td>
            <td @click="ViewDetails(records)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="ParticipateProjectPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>
    <div v-if="ifDetails" class="ParticipateProjectDetailsBackground">
      <div class="ParticipateProjectDetailsFormBackground">
        <span class="ParticipateProjectDetailsFormTitle">项目详细信息</span>
        <form class="ParticipateProjectDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>项目名称 : {{ data.projectName }}</label>
            </div>
            <div class="label-row">
              <label>项目来源 : {{ data.projectSource }}</label>
            </div>
            <div class="label-row">
              <label>项目形式 : {{ data.type }}</label>
              <label>排名 : {{ data.teamRank }}</label>
            </div>
            <div class="label-row">
              <label>开发技术 : {{ data.developTechnology }}</label>
            </div>
            <div class="label-row">
              <label>指导老师 : {{ data.instructor }}</label>
            </div>
            <div class="label-row">
              <label>开发时间 : {{ data.developTime }}</label>
              <label>历时 : {{ data.duration }}</label>
            </div>
            <div class="label-row">
              <label>有效代码数 : {{ data.numberOfValidCodes }}</label>
            </div>
            <div class="label-row">
              <label>主要创新点 : {{ data.mainInnovations }}</label>
            </div>
            <div class="label-row">
              <label>主要功能 : {{ data.mainFunction }}</label>
            </div>
            <div class="label-row">
              <label>对应文档 :</label>
              <a href="{{  data.signUpFile }}" target="_blank">{{
                data.signUpFile
              }}</a>
            </div>
            <br />
          </div>
        </form>
        <button
          class="ParticipateProjectDetailsFormCloseButton"
          @click="ParticipateProjectDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      StudentParticipateProjectTable: [],
      ifDetails: false,
      studentDetails: {},
      currentPage: 1,
      pageSize: 12,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getParticipateProject",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentParticipateProjectTable =
            informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },
    RenewLabel(response) {
      this.studentDetails = response.participateProjectDetails;
    },
    ParticipateProjectDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.ParticipateProjectPagination {
  position: absolute;
  top: 95%;
}
.ParticipateProjectUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.ParticipateProjectTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.ParticipateProjectTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/ParticipateProjectForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
